<template>
    <v-container class="fill-height" fluid>
        <v-row class="align-center justify-center">
            <v-col cols="12" sm="8" md="6" lg="4">
                <v-alert
                    v-model="alert"
                    border="left"
                    close-text="Close Alert"
                    :type="alertType"
                    transition="scale-transition"
                    prominent
                >
                    {{ alertMessage }}
                </v-alert>
                <v-fade-transition>
                    <v-card v-show="!loading" class="elevation-12 rounded-lg">
                        <v-toolbar :color="getAppColors.primary" dark>
                            <v-avatar size="50px">
                                <v-img
                                    alt="Logo"
                                    src="@/assets/logo-only.png"
                                    contain
                                />
                            </v-avatar>
                            <v-toolbar-title dark>
                                core<strong>CLEAR</strong> Portal
                            </v-toolbar-title>
                        </v-toolbar>
                        <v-card-text>
                            <v-form ref="form" v-model="valid">
                                <v-text-field
                                    prepend-icon="person"
                                    :color="getAppColors.primary"
                                    label="Username"
                                    name="username"
                                    type="text"
                                    autocomplete="username"
                                    v-model="username"
                                    :rules="usernameRules"
                                    required
                                />
                                <v-text-field
                                    prepend-icon="lock"
                                    :color="getAppColors.primary"
                                    :append-icon="
                                        showPassword ? 'mdi-eye' : 'mdi-eye-off'
                                    "
                                    label="Password"
                                    name="password"
                                    :type="showPassword ? 'text' : 'password'"
                                    autocomplete="current-password"
                                    v-model="password"
                                    :rules="passwordRules"
                                    @keyup.enter="submit"
                                    @click:append="showPassword = !showPassword"
                                    required
                                />
                            </v-form>
                        </v-card-text>
                        <v-card-actions>
                            <v-btn
                                :color="getAppColors.primary"
                                text
                                rounded
                                large
                                to="/forgot-password"
                            >
                                Forgot Password?
                            </v-btn>
                            <v-spacer />
                            <v-btn
                                :color="getAppColors.primary"
                                class="white--text"
                                :disabled="!valid"
                                :loading="loading"
                                @click="submit"
                                rounded
                                large
                            >
                                Login
                            </v-btn>
                        </v-card-actions>
                    </v-card>
                </v-fade-transition>
                <v-dialog
                    v-model="loading"
                    hide-overlay
                    persistent
                    transition="dialog-bottom-transition"
                    width="300"
                >
                    <v-card :color="getAppColors.primary" dark>
                        <v-card-text>
                            Logging in...
                            <v-progress-linear
                                indeterminate
                                color="white"
                                class="mb-0"
                            ></v-progress-linear>
                        </v-card-text>
                    </v-card>
                </v-dialog>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import axios from 'axios';
import { mapGetters } from 'vuex';

export default {
    name: 'LoginScreen',
    data() {
        return {
            loading: false,
            valid: false,
            showPassword: false,
            alert: false,
            alertMessage: '',
            alertType: 'success',
            username: '',
            password: '',
            usernameRules: [(v) => !!v || 'Username is required'],
            passwordRules: [(v) => !!v || 'Password is required'],
        };
    },
    computed: {
        ...mapGetters(['getAppColors', 'getInternalLoginController']),
        subdomain() {
            let full = window.location.host;
            let parts = full.split('.');

            return parts[0];
        },
    },
    methods: {
        openAlert(type, message) {
            this.alertType = type;
            this.alertMessage = message;
            this.alert = true;
        },
        submit() {
            if (this.$refs.form.validate()) {
                this.loading = true;
                this.alert = false;
                this.userLogin();
            }
        },
        async userLogin() {
            let formData = new FormData();
            formData.append('username', this.username);
            formData.append('password', this.password);

            try {
                let response = await axios.post(
                    `${this.getInternalLoginController + '/auth_only'}`,
                    formData,
                    {
                        withCredentials: true,
                        crossDomain: true,
                    },
                );

                this.loading = false;

                if (response.data.success === true) {
                    console.log('Logged in!');
                    this.$store.commit('setIsAuthenticated', true);
                    this.$store.commit(
                        'setCurrentLocationId',
                        response.data.currentLocationId,
                    );
                    this.$store.commit(
                        'setCurrentLocationName',
                        response.data.currentLocationName,
                    );
                    this.$store.commit(
                        'setAuthenticatedLocations',
                        response.data.authenticatedLocations,
                    );
                    this.$router.push('/');
                } else {
                    this.openAlert('error', response.data.error_message);
                }
            } catch (error) {
                console.error('Could not login');
                console.error(error);

                this.$store.commit('setIsAuthenticated', false);

                this.openAlert('error', error.toString());

                this.loading = false;
            }
        },
    },
};
</script>
